 пробував першу ложку, а потім трохи підкидав вгору. Якщо до стелі прилипне хоч трохи, то наступний рік буде врожайним. Зараз, звісно, ця традиція не виконується.Традиційно святвечір починається молитвою за здоров'я тих, хто живий, і за упокій тих, хто помер. Її виголошує хазяїн дому. Якщо за минулий рік у сім'ї хтось помер, то йому теж ставили тарілку і клали туди трохи куті.")])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('h1',{staticClass:"content-item-element content-item-title"},[_c('b',[_vm._v("Свята вечеря")])])}]

