          <h1 class="content-item-element content-item-title"><b>Звичаї</b></h1>
          <p class="content-item-element content-item-text">Традиційно Україна святкує Рождення Христове 7 січня. А перед тим 6 числа разом з першою (Віфлиємською) зіркою на небі. Уся родина сідає за спільну святкову вечерю біля Різдвяної ялинки, щоб відсвяткувати народження Ісуса.<br>
              Є певні традиції святкування Різдва, які присутні в усіх регіонах України.<br>
              Головна традиція – це свята вечеря, яка складається зі дванадцяти страв. Але "королевою" столу, звісно, є кутя. Колись із нею навіть проводили певний обряд. Господар хрестив страву, пробував першу ложку, а потім трохи підкидав вгору. Якщо до стелі прилипне хоч трохи, то наступний рік буде врожайним. Зараз, звісно, ця традиція не виконується.Традиційно святвечір починається молитвою за здоров'я тих, хто живий, і за упокій тих, хто помер. Її виголошує хазяїн дому. Якщо за минулий рік у сім'ї хтось помер, то йому теж ставили тарілку і клали туди трохи куті.</p>
          <img class="content-item-element content-item-image" src="./assets/images/traditions.jpg" alt="christmas image">
        </div>

        <div v-if="isSupper" class="content-item supper">
          <h1 class="content-item-element content-item-title"><b>Свята вечеря</b></h1>
          <img class="content-item-element content-item-image" src="./assets/images/supper.jpg" alt="christmas image">
          <p class="content-item-element content-item-text">Підготовка до святкової вечері мала всі ознаки церемоніалу та складалася з чіткої послідовності дій. Під образами на покуті стелили чисте сіно, а на нього ставили горщики з узваром і кутею і приказували: «Щоб кутя на покуть, а узвар на базар». Особливо підготовлювали стіл – його зверху притрушували соломою, по кутках клали часник як оберіг, а потім все вкривали скатертиною. На підлогу під стіл теж клали сіно, в яке було прийнято ховати сокиру та інші інструменти та господарські знаряддя із заліза, які за повір’ям мали захистити членів сім’ї від злих сил та забезпечити їм здоров’я. Усі предмети, які торкалися столу, за народними уявленнями, набували особливої сили і могли стати важливим магічним або ж лікувальним засобом.</p>
          <p class="content-item-element content-item-text">На вечерю готували дванадцять страв. Чому саме дванадцять страв? Етнографи кажуть, що це може бути пов’язано з дванадцятьма місяцями та певними календарними ритуалами у дохристиянські часи. У наш час число дванадцять трактують як згадку про дванадцять апостолів Ісуса Христа. Звичайно до Святвечора споживали всі продукти, наявні у господарстві, за винятком заборонених у піст. Різноманітність і багатство наїдків у цей день повинні були сприяти добробуту родини наступного року. Навіть найбідніші селяни докладали максимум зусиль, щоб гідно зустріти Різдво. Щоб встигнути приготувати дванадцять страв на святкову вечерю, господиня вставала дуже рано, розпалювала піч, обов’язково добуваючи «живий» вогонь (за допомогою шматків дерева, або кресала). У піч клала дванадцять полін. Для приготування страв набиралася досхідня вода, нею заливали пшеницю, сушені фрукти, і спершу ставили в піч варити дві головні страви – кутю та узвар.</p>
          <p class="content-item-element content-item-text">Кутя – варена пшениця з медом. Кутя з’явилася ще в дохристиянську добу та було глибоко символічною стравою. Пшениця, як зерно, щороку оживає, тому є символом вічності, а мед – символ вічного щастя святих у небі. Варили кутю у спеціальному горщику, в якому нічого іншого не варилося, або купували щораз новий. Узвар варився з сушених яблук, грушок, слив. На Поділлі узвар інколи ще називали сушеницею.</p>
          <p class="content-item-element content-item-text">Святкові страви починали куштувати як сходила вечірня зоря. Починали із куті. Поклавши в рукавицю гроші (щоб водились), господиня несла кутю на покуть, а діти під час цієї ходи завзято мекали, мукали, іржали, квоктали, крякали – щоб уся свійська живність плодилася, щоб курчата й каченята водилися. Перша ложка куті була для бджіл – кутю кидали до стелі, щоб рої велися. Друга – для самого морозу, щоб отак задобрений, він не чинив збитків, не морозив посівів. До того ж, серед подолян широко побутувало повір'я, що душі померлих родичів беруть участь у святковій трапезі, їх обов'язково згадували добрим словом. Для них після вечері залишали рештки їжі на столі (мити посуд у цей день вважалося гріхом). На ніч усі члени родини клали свої ложки у горщик з кутею. Це робилося для того, щоб святої каші могли поїсти предки.</p>
          <p class="content-item-element content-item-text">Повечерявши, брав господар зі столу потроху кожної із страв і ніс до хліва худобі, щоб не скаржилася. Бо, за повір'ям, у ніч під Різдво дарує їй Бог мову – за те, що колись у давнину спав у яслах маленький Христос. Кутю давали курям, «щоб гарно неслися». Її несли на гостинець хрещеним батькам, бабі-повитусі, родичам.</p>
          <p class="content-item-element content-item-text">На багату кутю відбувався також обмін ритуальними стравами. Вочевидь, ця традиція теж має дохристиянське коріння. Вечеря, що її посилали з дітьми бабі-повитусі, родичам, хрещеним батькам, як правило, складалася з куті, книшів, пирогів, риби тощо. Прийнявши обрядові страви, господарі дякували і обов’язково передавали дари у відповідь зі свого столу. В такий спосіб відбувався ритуальний обмін вечерею, який символізував спорідненість сімей та їх взаємну щедрість. Діток, які приносили вечерю, було прийнято пригощати та обдаровувати гостинцями (горіхами, яблуками, пиріжками, цукерками).</p>
        </div>


